import { TableHead } from '@material-ui/core'
import { TableRow } from '@material-ui/core'
import { TableBody } from '@material-ui/core'
import { Paper } from '@material-ui/core'
import { Table } from '@material-ui/core'
import { TableCell } from '@material-ui/core'
import { TableContainer } from '@material-ui/core'
import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import styled from 'styled-components'
import { db, st } from './firebase'
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import { Fab } from '@material-ui/core'
import {motion} from 'framer-motion'



export default function ProductTable() {
    const [items, setItems] = useState([])

    useEffect(() => {
        db.collection('knives').orderBy('timestamp', 'desc').onSnapshot(snap => (
            setItems(snap.docs.map(doc => (
                {
                    id: doc.id,
                    data: doc.data()
                }
            )))
        ))
        console.log(items)
        return;
    }, [])


    const handleDelete = ({id, name, img}) => {
        if (window.confirm(`Sigur ștergi produsul "${name}"?`) === true) {
            db.collection('knives').doc(id).delete()
            st.refFromURL(img).delete()
        }
    }

    return (
        <Container>
            <TableContainer component={Paper}>
                <Table style={{minWidth: 650}} aria-label="simple table" dense table size="small">
                    <TableHead style={{backgroundColor: "#505050"}}>
                        <TableRow>
                            <HeadCell>Imagine</HeadCell>
                            <HeadCell align="left">Nume Produs</HeadCell>
                            <HeadCell align="right">ID</HeadCell>
                            <HeadCell align="left">Descriere</HeadCell>
                            <HeadCell align="right">Preț</HeadCell>
                            <HeadCell align="right">Stoc</HeadCell>
                            <HeadCell align="right">Op1</HeadCell>
                            <HeadCell align="right">Op2</HeadCell>
                            <HeadCell align="right">Op3</HeadCell>
                            <HeadCell align="right">Op4</HeadCell>
                            <HeadCell align="right">Șterge</HeadCell>
                        </TableRow>
                    </TableHead>
                    <TableBody style={{maxHeight: '100vh' , overflowY: 'scroll'}}>
                            {items && items.map(({id, data}) => (
                                <TableRow key={id} title={new Date(data.timestamp?.toDate()).toString().slice(0, 25)}>
                                    <TableCell component="th" scope="row">
                                        <ProductImage 
                                            src={data.image} 
                                            alt={data.name} 
                                            as={motion.img}
                                            initial={{scale: 0}}
                                            animate={{scale: 1}}
                                            transition={{delay: 1}}
                                        />
                                    </TableCell>
                                    <TableCell align="left">{data.name}</TableCell>
                                    <TableCell align="right">{data.id}</TableCell>
                                    <TableCell align="left">{data.description}</TableCell>
                                    <TableCell align="right">{data.price}</TableCell>
                                    <TableCell align="right">{data.stock}</TableCell>
                                    <TableCell align="right">{data.option1}</TableCell>
                                    <TableCell align="right">{data.option2}</TableCell>
                                    <TableCell align="right">{data.option3}</TableCell>
                                    <TableCell align="right">{data.option4}</TableCell>
                                    <TableCell align="right" onClick={() => handleDelete({id, name: data.name, img: data.image})}>
                                        <Fab size="small" >
                                            <DeleteOutlineIcon color="secondary"/>
                                        </Fab>
                                    </TableCell>
                                </TableRow>
                            ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Container>
    )
}



const Container = styled.aside`
    margin: 100px auto;
    max-width: 95%;
    table tbody {
        >:nth-child(even) {
            background-color: #f3f3f3;
        }
    }
`
const HeadCell = styled(TableCell)`
    color: white !important;
`
const ProductImage = styled.img`
    overflow: hidden;
    display: block;
    max-width: 100px;
    max-height: 50px;
`